// src/app/remontDvs/[slug]/page.tsx
import fs from 'fs';
import path from 'path';

export default function RemontDvsSpecial({ params }) {
  const { slug } = params;

  const filePath = path.join(process.cwd(), '../data/dvs', `${slug}.json`);
  const fileContent = fs.readFileSync(filePath, 'utf-8');
  const data = JSON.parse(fileContent);

  return (
    <div className="RemontDvsSpecial">
      <h1>Ремонт ДВС {slug}</h1>

      <div className="engine-info">
        <h2>Слабые места</h2>
        <p>{data.slabie_mesta}</p>

        <h2>Как ремонтировать</h2>
        <p>{data.kak_remontirovat}</p>

        <h2>Как увеличить срок службы</h2>
        <p>{data.kak_uvelichit_srok_slujby}</p>

        <h2>Как обслуживать</h2>
        <p>{data.kak_obslujivat}</p>
      </div>
    </div>
  );
}
